.Overlay {
  position: absolute;
  top: 0;
  right: 0;
  /*margin: 20px;*/
  background: black;
  width: 400px;
  padding: 20px;
  overflow: scroll;
  height: 100vh;

}

.Overlay img {
  margin: 30px auto;
  display: block;
  width: 100%;

}

button {
  background-color: black; 
  float: right;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 30px;
  margin: 4px 4px 4px 4px;
  border: none;

}